import React, { useState, useEffect } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { CategoryScale, LinearScale } from 'chart.js';
import Chart from 'chart.js/auto';
import Select from 'react-select';
import Api from '../helper/Api';
import ShowChart from '../helper/ShowChart';
import '../css/DatePicker.css';
import '../css/Chart.css';


Chart.register(CategoryScale, LinearScale);

function DataPage() {
  const [data, setData] = useState([]);
  const [availableDates, setAvailableDates] = useState([]);
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedId, setSelectedId] = useState(0); // Initial selected value for dropdown
  const [dropdownOptions, setDropdownOptions] = useState([]); // State variable for dropdown options

  useEffect(() => {
    Api.getIdRumahKaca() // Fetch the options from the API
      .then((res) => {
        const options = res.data.map((d) => ({ value: d.ID_Rumah_Kaca, label: d.ID_Rumah_Kaca }));
        setDropdownOptions(options);
        setSelectedId(options[0].value); // Set the initial selected value
      })
      .catch((err) => console.log(err));
  }, []);

  useEffect(() => {
    Api.getAvailableDates(selectedId) // Pass selected value to the API call
      .then((res) => {
        const datesWithAvailableData = res.data.map((d) => d.Tanggal);
        setAvailableDates(datesWithAvailableData);
        if (datesWithAvailableData.length > 0) {
          setSelectedDate(new Date(datesWithAvailableData[0]));
        }
      })
      .catch((err) => console.log(err));
  }, [selectedId]); // Update when selectedId changes

  useEffect(() => {
    if (selectedDate) {
      Api.getDataByDate(selectedDate.toISOString().slice(0, 10), selectedId) // Pass selected value to the API call
        .then((res) => {
          setData(res.data);
        })
        .catch((err) => console.log(err));
    }
  }, [selectedDate, selectedId]); // Update when selectedDate or selectedId changes

  const filterDate = (date) => {
    if (date) {
      const formattedDate = date.toISOString().slice(0, 10);
      const nextDate = new Date(date);
      nextDate.setDate(nextDate.getDate() + 1);
      const formattedNextDate = nextDate.toISOString().split('T')[0];
  
      return (
        (availableDates.includes(formattedDate) && availableDates.includes(formattedNextDate)) ||
        (!availableDates.includes(formattedDate) && availableDates.includes(formattedNextDate))
      );
    }
    return false;
  };

  const handleDropdownChange = (selectedOption) => {
    setSelectedId(selectedOption.value);
  };

  return (
    <div className="container">
      <div className="chart-container">
        <div className="input-container">
          <div className="left-column">
          <label htmlFor="dropdown">Greenhouse:</label>
              <Select
                id="dropdown"
                value={dropdownOptions.find((option) => option.value === selectedId)}
                options={dropdownOptions}
                onChange={handleDropdownChange}
              />
          </div>
          <div className="right-column">
            <label htmlFor="datepicker">Date:</label>
            {availableDates.length > 0 && (
              <DatePicker
                id="datepicker"
                selected={selectedDate}
                onChange={(date) => setSelectedDate(date)}
                filterDate={filterDate}
                dateFormat="yyyy-MM-dd"
              />
            )}
          </div>
        </div>
        <ShowChart data={data} scale={'Suhu'} mode={'Avg'} />
        <br />
        <br />
        <ShowChart data={data} scale={'RH'} mode={'Avg'} />
        <br />
        <br />
      </div>
    </div>
  );
}

export default DataPage;
