import axios from 'axios';

const baseApi = 'http://www.yessaunpad.web.id/monApi';
// const baseApi = 'http://localhost:5005';

class Api {
  static getAvailableDates(idRumahKaca) {
    return axios.get(`${baseApi}/available-dates?idRumahKaca=${idRumahKaca}`);
  }

  static getDataByDate(date, idRumahKaca) {
    return axios.get(`${baseApi}/data?date=${date}&idRumahKaca=${idRumahKaca}`);
  }

  static getDataByMonth(month, idRumahKaca) {
    return axios.get(`${baseApi}/monthly-data?month=${month}&idRumahKaca=${idRumahKaca}`);
  }

  static getIdRumahKaca() {
    return axios.get(`${baseApi}/id-rumah-kaca`);
  }

  static downloadData(date, idRumahKaca) {
    return axios.get(`${baseApi}/download-data?date=${date}&idRumahKaca=${idRumahKaca}`);
  }
}

export default Api;